<template>
  <f7-page
    @page:init="onPageInit"
    @page:beforeout="onBeforeOut"
    class="paper-page"
  >
    <f7-page-content @scroll.native="onScroll">
      <f7-block
        class="text-align-center"
        v-if="isLoading">
        <f7-preloader></f7-preloader>
      </f7-block>

      <f7-block
        class="paper-block-content"
      >
        <h1>{{title}}</h1>
        <p
          v-for="(item, index) in pageData"
          :key="index"
        >
          {{item.en}}
        </p>
      </f7-block>
    </f7-page-content>
  </f7-page>
</template>

<script>
import { f7Preloader, f7PageContent } from "framework7-vue";
import http from "@/utils/http";
import api from "@/utils/api";
import mixin from "@/mixin";
const region = "content";

export default {
  components: {
    f7Preloader,
    f7PageContent
  },

  mixins: [mixin],

  data() {
    return {
      contentRegion: "",
      isLoading: true,
      region,
      link: "",
      url: "",
      title: ""
    };
  },

  methods: {
    onPageInit() {
      let { url, region } = this.$f7route.query;

      this.getData({ url, region });
      this.contentRegion = region;
      this.url = url;
      this.link = encodeURIComponent(url);

      this.$f7.progressbar.show(0, "orange");
    },

    getData({ url, region }) {
      let { title } = this.$f7route.query;
      let payload = {
        url,
        region
      };

      return Promise.all([
        this.$store.dispatch(`${this.region}/getContent`, payload),
        this.$store.dispatch(`${this.region}/getProgress`, payload)
      ])
        .then(() => {})
        .catch(err => {
          console.error(err);
        })
        .finally(() => {
          this.isLoading = false;
          this.title = title;
        });
    },

    onScroll(e) {
      let { scrollTop, scrollHeight, offsetHeight } = e.target;
      let progress = (scrollTop / (scrollHeight - offsetHeight)) * 100;

      this.$f7.progressbar.show(progress, "orange");
    },

    onBeforeOut() {
      this.$f7.progressbar.hide();
    }
  },

  computed: {
    pageData() {
      return this.$store.state[this.region].contentGroup[
        `/${this.url}/${this.contentRegion}`
      ];
    },

    progress() {
      return this.$store.state[this.region].progressGroup[
        `/${this.url}/${this.contentRegion}`
      ];
    }
  }
};
</script>

